<template>
    <div class="container">
        <Title greeting-message="學報徵稿"></Title>
        <div class="vue-html" v-html="modifyUrl(news.context)"></div>
        <div class="text-center">
            <button type="button" class="btn btn-secondary my-3" @click="historyPrev">回上頁</button>
        </div>
    </div>
</template>

<script>
import Title from '../components/Title.vue';
import { NewsServer } from "../../providers/http-server.js";
import moment from 'moment';
let page = 6

export default {
    name: 'App',
    data() {
        return {
            news: []
        }
    },
    components: { 
        Title
    },
    created(){
        this.moment = moment;
        NewsServer.item(page).then(
            (response) => {
                this.news = response.data;
                this.news_type = response.data.notification.type;
            }
        )
    },
    methods: {
        historyPrev() {
            this.$router.go(-1);
        },
        modifyUrl(url) {
            let endpoint = url;
            endpoint = endpoint.replace('<oembed url=', '<iframe src=');
            endpoint = endpoint.replace('https://youtu.be/', 'https://www.youtube.com/embed/');
            endpoint = endpoint.replace('</oembed>', '</iframe>');
            return endpoint;
        },
    }
};
</script>

<style scoped>
@import '../../libs/ckeditor_plugin.css';
</style>